/* app/globals.css - Полностью обновленная система дизайна */

@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&family=Chakra+Petch:wght@300;400;500;600;700&display=swap');

:root {
  /* Цветовая система - Футуристичная неоновая палитра */
  --primary-deep: #0A0118;
  --primary-dark: #1A0B2E;
  --primary-purple: #8A2BE2;
  --primary-pink: #FF6B9D;
  --primary-cyan: #4DE3C8;
  --primary-blue: #6B8AFF;
  --primary-gold: #FFD166;
  
  /* Градиенты */
  --gradient-hero: linear-gradient(135deg, #0A0118 0%, #1A0B2E 50%, #2D1B69 100%);
  --gradient-neon: linear-gradient(90deg, #8A2BE2 0%, #FF6B9D 50%, #4DE3C8 100%);
  --gradient-card: linear-gradient(145deg, rgba(26, 11, 46, 0.8), rgba(42, 17, 87, 0.4));
  --gradient-glow: linear-gradient(45deg, #8A2BE2, #FF6B9D, #4DE3C8, #6B8AFF);
  
  /* Тени и свечения */
  --shadow-neon-purple: 0 0 20px rgba(138, 43, 226, 0.5),
                        0 0 40px rgba(138, 43, 226, 0.3),
                        0 0 60px rgba(138, 43, 226, 0.1);
  --shadow-neon-pink: 0 0 20px rgba(255, 107, 157, 0.5),
                      0 0 40px rgba(255, 107, 157, 0.3);
  --shadow-neon-cyan: 0 0 20px rgba(77, 227, 200, 0.5),
                      0 0 40px rgba(77, 227, 200, 0.3);
  --shadow-card: 0 20px 40px rgba(0, 0, 0, 0.4),
                 inset 0 1px 0 rgba(255, 255, 255, 0.1);
  
  /* Анимационные константы */
  --ease-bounce: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  --ease-snap: cubic-bezier(0.87, 0, 0.13, 1);
  --ease-power: cubic-bezier(0.77, 0, 0.175, 1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  background: var(--primary-deep);
}

body {
  font-family: 'Space Grotesk', sans-serif;
  background: var(--gradient-hero);
  color: #F5F0FF;
  min-height: 100vh;
  overflow-x: hidden;
  background-attachment: fixed;
}

/* === ТИПОГРАФИЯ === */
.display-1 {
  font-family: 'Chakra Petch', sans-serif;
  font-size: clamp(3.5rem, 8vw, 8rem);
  font-weight: 700;
  line-height: 1.1;
  background: var(--gradient-neon);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  animation: text-glow 4s ease-in-out infinite alternate;
}

.display-2 {
  font-family: 'Chakra Petch', sans-serif;
  font-size: clamp(2.5rem, 6vw, 4.5rem);
  font-weight: 600;
  line-height: 1.2;
}

.display-3 {
  font-family: 'Chakra Petch', sans-serif;
  font-size: clamp(2rem, 5vw, 3rem);
  font-weight: 600;
  line-height: 1.3;
}

.body-xl {
  font-size: 1.5rem;
  line-height: 1.6;
  font-weight: 300;
}

.body-lg {
  font-size: 1.25rem;
  line-height: 1.6;
  font-weight: 300;
}

.body-md {
  font-size: 1rem;
  line-height: 1.6;
  font-weight: 300;
}

/* === КОМПОНЕНТЫ === */
/* Неоновые кнопки */
.btn-neon {
  position: relative;
  padding: 1.25rem 2.5rem;
  background: transparent;
  border: 2px solid transparent;
  border-radius: 50px;
  font-family: 'Chakra Petch', sans-serif;
  font-weight: 600;
  font-size: 1.125rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #FFF;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.6s var(--ease-power);
  transform-style: preserve-3d;
  perspective: 1000px;
}

.btn-neon::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--gradient-neon);
  border-radius: 50px;
  z-index: -2;
  animation: gradient-shift 4s ease infinite;
}

.btn-neon::after {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  right: 2px;
  bottom: 2px;
  background: var(--primary-dark);
  border-radius: 48px;
  z-index: -1;
  transition: all 0.6s var(--ease-power);
}

.btn-neon:hover {
  transform: translateY(-8px) scale(1.05);
  box-shadow: var(--shadow-neon-purple),
              0 20px 40px rgba(0, 0, 0, 0.5);
}

.btn-neon:hover::after {
  background: rgba(26, 11, 46, 0.8);
}

.btn-neon:active {
  transform: translateY(-4px) scale(0.98);
  transition: all 0.2s var(--ease-snap);
}

/* Стеклянные карточки */
.glass-card {
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.05) 0%,
    rgba(255, 255, 255, 0.02) 100%
  );
  backdrop-filter: blur(20px);
  border-radius: 30px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: var(--shadow-card);
  transition: all 0.8s var(--ease-power);
  position: relative;
  overflow: hidden;
}

.glass-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.1),
    transparent
  );
  transition: 0.5s;
}

.glass-card:hover::before {
  left: 100%;
}

.glass-card:hover {
  transform: translateY(-10px) scale(1.02);
  border-color: rgba(138, 43, 226, 0.3);
  box-shadow: var(--shadow-neon-purple),
              0 30px 60px rgba(0, 0, 0, 0.5);
}

/* Анимации */
@keyframes float {
  0%, 100% {
    transform: translateY(0px) rotate(0deg);
  }
  33% {
    transform: translateY(-20px) rotate(5deg);
  }
  66% {
    transform: translateY(10px) rotate(-5deg);
  }
}

@keyframes pulse-glow {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.8;
    transform: scale(1.05);
  }
}

@keyframes gradient-shift {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

@keyframes text-glow {
  0% {
    filter: drop-shadow(0 0 10px rgba(138, 43, 226, 0.5));
  }
  33% {
    filter: drop-shadow(0 0 15px rgba(255, 107, 157, 0.5));
  }
  66% {
    filter: drop-shadow(0 0 15px rgba(77, 227, 200, 0.5));
  }
  100% {
    filter: drop-shadow(0 0 10px rgba(138, 43, 226, 0.5));
  }
}

@keyframes morph {
  0%, 100% {
    border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
  }
  50% {
    border-radius: 30% 60% 70% 40% / 50% 60% 30% 60%;
  }
}

@keyframes slide-in {
  from {
    opacity: 0;
    transform: translateY(50px) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes slide-in-left {
  from {
    opacity: 0;
    transform: translateX(-100px) rotate(-10deg);
  }
  to {
    opacity: 1;
    transform: translateX(0) rotate(0);
  }
}

@keyframes slide-in-right {
  from {
    opacity: 0;
    transform: translateX(100px) rotate(10deg);
  }
  to {
    opacity: 1;
    transform: translateX(0) rotate(0);
  }
}

/* Классы анимаций */
.animate-float {
  animation: float 6s var(--ease-bounce) infinite;
}

.animate-pulse-glow {
  animation: pulse-glow 2s var(--ease-power) infinite;
}

.animate-morph {
  animation: morph 8s ease-in-out infinite;
}

.animate-slide-in {
  animation: slide-in 1s var(--ease-power) forwards;
}

.animate-slide-in-left {
  animation: slide-in-left 1.2s var(--ease-power) forwards;
}

.animate-slide-in-right {
  animation: slide-in-right 1.2s var(--ease-power) forwards;
}

.animate-delay-1 { animation-delay: 0.1s; }
.animate-delay-2 { animation-delay: 0.2s; }
.animate-delay-3 { animation-delay: 0.3s; }
.animate-delay-4 { animation-delay: 0.4s; }
.animate-delay-5 { animation-delay: 0.5s; }

/* Частицы */
.particles-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -1;
}

.particle {
  position: absolute;
  background: var(--gradient-neon);
  border-radius: 50%;
  filter: blur(1px);
  opacity: 0.7;
}

/* Dock Navigation (улучшенная версия) */
.dock-nav {
  position: fixed;
  top: 2rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  gap: 1.5rem;
  padding: 1.25rem 2.5rem;
  background: rgba(26, 11, 46, 0.95);
  backdrop-filter: blur(20px);
  border-radius: 50px;
  border: 2px solid rgba(138, 43, 226, 0.2);
  box-shadow: var(--shadow-neon-purple),
              0 20px 60px rgba(0, 0, 0, 0.4);
  z-index: 1000;
  transition: all 0.8s var(--ease-power);
}

.dock-nav.scrolled {
  top: 1rem;
  padding: 1rem 2rem;
  background: rgba(10, 1, 24, 0.98);
  border-color: rgba(77, 227, 200, 0.3);
  box-shadow: var(--shadow-neon-cyan),
              0 15px 50px rgba(0, 0, 0, 0.5);
}

.dock-logo {
  width: 50px;
  height: 50px;
  background: var(--gradient-neon);
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: pulse-glow 3s var(--ease-power) infinite;
  cursor: pointer;
  transition: all 0.4s var(--ease-bounce);
}

.dock-logo:hover {
  transform: rotate(15deg) scale(1.2);
  box-shadow: 0 0 40px rgba(138, 43, 226, 0.8);
}

.dock-items {
  display: flex;
  gap: 0.75rem;
}

.dock-link {
  position: relative;
  color: #F5F0FF;
  text-decoration: none;
  font-weight: 600;
  font-size: 1rem;
  padding: 0.75rem 1.5rem;
  border-radius: 25px;
  transition: all 0.4s var(--ease-bounce);
  z-index: 1;
}

.dock-link::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--gradient-neon);
  border-radius: 25px;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.4s var(--ease-power);
}

.dock-link:hover {
  color: white;
  transform: translateY(-5px) scale(1.1);
}

.dock-link:hover::before {
  opacity: 0.2;
}

.dock-link.active {
  background: var(--gradient-neon);
  color: white;
  box-shadow: var(--shadow-neon-purple);
  animation: pulse-glow 2s var(--ease-power) infinite;
}

.dock-wallet-btn {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.875rem 1.75rem;
  background: var(--gradient-neon);
  color: white;
  border: none;
  border-radius: 25px;
  font-family: 'Chakra Petch', sans-serif;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.4s var(--ease-bounce);
  animation: gradient-shift 4s ease infinite;
}

.dock-wallet-btn:hover {
  transform: translateY(-5px) scale(1.1);
  box-shadow: var(--shadow-neon-purple),
              0 15px 40px rgba(0, 0, 0, 0.4);
}

/* Эффекты появления */
.reveal {
  opacity: 0;
  transform: translateY(30px);
  transition: all 1s var(--ease-power);
}

.reveal.active {
  opacity: 1;
  transform: translateY(0);
}

/* Контейнеры и сетки */
.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
}

.section {
  padding: 8rem 0;
  position: relative;
  overflow: hidden;
}

.grid {
  display: grid;
  gap: 2rem;
}

.grid-cols-2 { grid-template-columns: repeat(2, 1fr); }
.grid-cols-3 { grid-template-columns: repeat(3, 1fr); }
.grid-cols-4 { grid-template-columns: repeat(4, 1fr); }

/* Формы */
.form-input {
  width: 100%;
  padding: 1.25rem 1.5rem;
  background: rgba(255, 255, 255, 0.05);
  border: 2px solid transparent;
  border-radius: 20px;
  color: white;
  font-family: 'Space Grotesk', sans-serif;
  font-size: 1rem;
  transition: all 0.4s var(--ease-power);
}

.form-input:focus {
  outline: none;
  border-color: var(--primary-cyan);
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 20px rgba(77, 227, 200, 0.3);
}

/* Планшеты */
@media (max-width: 1024px) {
  .display-1 { font-size: clamp(3rem, 7vw, 6rem); }
  .display-2 { font-size: clamp(2rem, 5vw, 3.5rem); }
  .grid-cols-3 { grid-template-columns: repeat(2, 1fr); }
  .dock-nav { padding: 1rem 1.5rem; }
}

/* Мобильные */
@media (max-width: 768px) {
  .section { padding: 4rem 0; }
  .grid-cols-2,
  .grid-cols-3,
  .grid-cols-4 { grid-template-columns: 1fr; }
  .dock-nav {
    top: 1rem;
    width: 95%;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
  }
  .dock-items {
    order: 3;
    width: 100%;
    justify-content: center;
    flex-wrap: wrap;
  }
  .body-xl { font-size: 1.25rem; }
}